@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  font-family: sans-serif;
  text-align: center;
}

.glitch-rgb {
  inset: 0;
  mix-blend-mode: screen;
  z-index: -1;
  pointer-events: none;
}

.glitch-rgb::before,
.glitch-rgb::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: inherit;
  background-size: cover;
  background-position: center;
  opacity: 0.12;
  pointer-events: none;
  mix-blend-mode: screen;
  z-index: -1;
}

.glitch-rgb::before {
  animation: glitch-rgb-1 2.5s infinite linear alternate-reverse;
  filter: drop-shadow(2px 0 red);
}

.glitch-rgb::after {
  animation: glitch-rgb-2 2.5s infinite linear alternate-reverse;
  filter: drop-shadow(-2px 0 cyan);
}

@keyframes glitch-rgb-1 {
  0% {
    clip-path: inset(5% 0 90% 0);
    transform: translate(0, 0);
  }
  20% {
    clip-path: inset(30% 0 50% 0);
    transform: translate(3px, -3px);
  }
  40% {
    clip-path: inset(10% 0 70% 0);
    transform: translate(-4px, 2px);
  }
  60% {
    clip-path: inset(40% 0 20% 0);
    transform: translate(2px, -5px);
  }
  80% {
    clip-path: inset(25% 0 40% 0);
    transform: translate(-2px, 2px);
  }
  100% {
    clip-path: inset(5% 0 90% 0);
    transform: translate(0, 0);
  }
}

@keyframes glitch-rgb-2 {
  0% {
    clip-path: inset(0 0 85% 0);
    transform: translate(0, 0);
  }
  20% {
    clip-path: inset(50% 0 30% 0);
    transform: translate(-3px, 3px);
  }
  40% {
    clip-path: inset(60% 0 10% 0);
    transform: translate(5px, -2px);
  }
  60% {
    clip-path: inset(20% 0 40% 0);
    transform: translate(-4px, 5px);
  }
  80% {
    clip-path: inset(35% 0 25% 0);
    transform: translate(3px, -3px);
  }
  100% {
    clip-path: inset(0 0 85% 0);
    transform: translate(0, 0);
  }
}

body {
  scrollbar-color: #81e48e00 #43ec7c00 !important;
  scrollbar-width: none;
}

html {
  scrollbar-color: #ffffff #f00add !important;
  scrollbar-width: none;
}
